import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from "@/components/mbbo/navigation/navigation.vue";
import Buyer from "./my-BuyeronAlready/my-Buyer";
import Already from "./my-BuyeronAlready/my-Already";

import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			list: [],
			num: 0,
			Mess:[],
			Alr:[],
		}
	},
	components: {
		Buyer,
		Already,
		quickBtn,
		navigation
	},
	computed: {
		i18n () {
		  return this.$t('Mypersonal')  
		},
		...mapGetters(['GET_HOME', 'QUICKNAVCO'])
	},
	onLoad:function(e){
		console.log(e.id)
		if(e.id === '1'){
			this.num=0;
		}else if(e.id === '2'){
			this.num=1;
		}
		uni.setNavigationBarTitle({
			title: this.i18n.message1
		});
		this.list.push(this.i18n.message2)
		this.list.push(this.i18n.message3)
	},
	created() {
		//收到消息列表
		this.Message();
		//发给别人的留言
		this.Already();
	},
	methods:{
		gotoGood(index) {
			this.num = index;
		},
		// 收到消息列表
		Message(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/msg/selectList?userId=760&page=1', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.Mess = response.data.content.list
						this.$store.commit('setnews', this.Mess);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		//发给别人的留言
		Already(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/msg/selectList?myUserId=760&page=1', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.Alr = response.data.content.list
						this.$store.commit('setAlr', this.Alr);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
	
}
